        'opacity' : '1'
      };
      this.opacityElements = {
        'transition': '.2s opacity',
        'opacity': '1'
      },
      this.words = this.languageOn.words
      this.numberWords = this.languageOn.numberWords
      // timeDebonce = this.timeSpent


      //loading words
      for(let i=0;i<9;i++){
        const x = Math.floor(Math.random()*this.numberWords);
        this.wordsOnPage[i].word =  this.words[x].word;
      }

      this.checkLetterAndPushWordsFunction()

      // this.checkLetterInterval = setInterval(this.checkLetterAndPushWordsFunction,0)
      clearInterval(this.checkLetterInterval)
    },

    // funkcja do zmiennej timer (do metody setInterval())
    // funkcja ma za zadanie zmniejszac czas przeznaczony na pisanie o -1 i
    // przekazywac dane do tablicy, ktora jest potrzebna do wykresow , a czas bedzie rowny 0
    // function for use in timer
    timerFunction(){
      if(this.timeSpent == 0){
        this.stopTime = false;
        this.AFKdetectorEnabled = false;
        this.reloaded = false
      }
      else{
        if(this.stopTime){
          this.timeSpent--;
          this.timeSpentPlus++;
          this.wpmChartArray.push(this.wordsPerMinute);
          this.wpmChartTimeArray.push(this.timeSpentPlus);
          this.AFKdetectorEnabled = true;
        }
      }
    },


    // sprawdzanie poprawnosci sowa oraz podwietlanie klawiszy, ktore zostana nacinite (dla klawiatury ekranowej)
    // word validation and backlight of the keys to be pressed (for screen keyboard)
    checkWord(e){
      this.showBody = {
        'transition': '.2s opacity',
        'opacity': '0'
      };

     this.opacityElements = {
        'transition': '.2s opacity',
        'opacity': '.5'
      },

      this.checkLetterInterval = setInterval(this.checkLetterAndPushWordsFunction,0)

      if(this.stopTime === false) this.timer = setInterval(this.timerFunction, 1000)

      this.stopTime = true;
      this.marginLineBehindLetter.animationIterationCount = 0;

      // podwietlanie klawiszy, ktore zostana nacinite (dla klawiatury ekranowej)
      // backlight of the keys to be pressed (for screen keyboard)
      for(let i=0;i<5;i++){
        for(let j=0;j<this.keyboard[i].length; j++){
          if(e.code == this.keyboard[i][j].code){
            this.keyboard[i][j].active = {'background-color': this.colorSub}
            setTimeout(()=>{
              this.keyboard[i][j].active = {'background-color': this.bgMain}
            },150)
          }
        }
      }

      // sprawdzania poprawnosci sowa i aktualizowanie danych
      // word validation and updating data
      if(e.keyCode === 32){

        e.preventDefault();

        if(this.valueInput == this.wordsOnPage[0].word){
          this.goodLetters += this.wordsOnPage[0].word.length + 1;
          this.wordsOnPage.shift();
        }
        else if(this.valueInput == '') return
        else{
          this.wrongLetters += this.wordsOnPage[0].word.length + 1;
          this.wordsOnPage.shift();
        }


        this.accuracy = (this.goodLetters/(this.goodLetters+this.wrongLetters)*100).toFixed(2);

        this.valueInput = '';
        this.letterOfValue = '';
        this.numberOfLetter = 0;
      }
    },

    opacityTimeSpent(){
      this.opacityColorTimeSpent = !this.opacityColorTimeSpent;
      this.normalColorTimeSpent = !this.normalColorTimeSpent;
    },

    // po kliknieciu na element input dostaje focus
    // after click the element getting focus
    focusInputOnClick(){
      setTimeout(() => this.focusInput = false, 0);
      this.$refs.focusInputWrite.focus();
    },

    // nie wyswietla komuniktu, e input nie ma focus a
    // doesn't display the message that input has no focus
    whileFocusInput(){
      this.focusInput = false;
    },

    // wyswietla komuniktu, e input nie ma focus a
    // displays the message that input has no focus
    unFocusInput(){
      this.showBody = {
        'transition': '.2s opacity',
        'opacity': '1'
      };

     this.opacityElements = {
        'transition': '.2s opacity',
        'opacity': '1'
      },
      this.focusInput = true;
    },

    // zamyka lub wyswietla ustawienia
    // exits or displays settings
    showLayerSett(){
      this.layerTopUsers = false;
      this.layerSett = !this.layerSett;
      this.showMain = !this.showMain;
      this.alertSuccess = false;
      this.reload();
    },

    // zamyka lub wyswietla najlepszych zajerestrowanych uytkownikw
    // closes or displays top registered users
    async showLayerTopUsers(){
      this.layerTopUsers = !this.layerTopUsers
      this.layerSett = false
      this.showMain = !this.showMain;

      await axios
      .get('http://localhost:3000/users/topPlayers')
      .then((response) =>{
        this.topUsers15 = response.data.topUsers15
        this.topUsers60 = response.data.topUsers60
      })
      .catch((err) => console.log(err))
      this.reload();
    },

    // zmienia czasu na ukoczenie testu
    // changes the time to complete the test
    changeTimeSpent(timeToType){
      this.timeSpent = timeToType;
      this.timeToTypeStorage = timeToType;

      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zmienia czasu na ukoczenie testu, czas jest podany przez uytkownika
    // changes the time to complete the test, the time is given by the user
    changeTimeSpentOptional(){
      if(this.value < 5) this.value = 5;

      this.value = parseInt(this.value)
      Math.floor(this.value)

      this.timeSpent = this.value;
      this.timeToTypeStorage = this.value;

      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zmiana typu pisania na 1 (chodzi o sposob poruszania sie lini wskazujcej gdzie aktualnie sie pisze)
    // changes the type of typing to 1 (it's about the way the line indicating where is currently typing moves)
    changeTypeOfWriting1(){
      this.marginLineBehindLetter.type = false;
      this.marginWordAfterKeyup.type = true;
      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zmiana typu pisania na 2 (chodzi o sposob poruszania sie lini wskazujcej gdzie aktualnie sie pisze)
    // change the type of typing to 2 (it's about the way the line indicating where is currently typing moves)
    changeTypeOfWriting2(){
      this.marginLineBehindLetter.type = true;
      this.marginWordAfterKeyup.type = false;
      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // ukrycie lub pokazanie wynikow na ywo
    // hide or show live results
    showLiveScore(){
      if(this.showScore === 'OFF'){
        this.showScore = 'ON'
        this.showScoreBool = true
      }
      else{
        this.showScore = 'OFF'
        this.showScoreBool = false
      }
      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // ukrycie lub pokazanie klawiatury ekranowej
    // hide or show screen keyboard
    showKeyboardFunction(){
      if(this.showKeyboard === 'OFF'){
        this.showKeyboard = 'ON'
        this.showKeyboardBool = true
      }
      else{
        this.showKeyboard = 'OFF'
        this.showKeyboardBool = false
      }
      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zmiana jzyka, ktry uytkownik wybierze
    // changes the language that the user selects
    changeLanguage(language){
      for(let i = 0; i<this.languagesArray.length; i++)
        if(language == this.languagesArray[i].json) this.languageOn = this.languagesArray[i].json

      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zmiana jzyka (w tym przypadku jzykami s literki z alfabetu) , ktry uytkownik wybierze
    // change the language (in this case the languages are letters from the alphabet), which the user selects
    changeLanguageAlphabet(alphabet){
      for(let i = 0; i<this.alphabetArray.length; i++)
        if(alphabet == this.alphabetArray[i].json) this.languageOn = this.alphabetArray[i].json

      this.alertSuccess = true
      setTimeout(() => this.alertSuccess = false, 2000)
    },

    // zamykanie alertu po wybraniu jednego z ustawie
    // close an alert after selecting one of the settings
    closeAlert(){
      this.alertSuccess = false
    },

    // wykrywa czy uytkownik nie jest AFK (away from keyboard) czyli czy pisze
    // detects whether the user is AFK (away from keyboard)
    AFKdetector:
      debounce(
        AFKdetectorDebounced
      , timeDebonce*300)
    ,

    // odwieanie zmiennych
    // refreshing variables
    reload(){
      this.valueInput = '';
      this.timeSpent = 0;
      this.timeSpent = this.timeToTypeStorage;
      this.timeSpentPlus = 0;
      this.wordsPerMinute = 0;
      this.wrongLetters = 0;
      this.goodLetters = 0;
      this.accuracy = 0;
      this.stopTime = false;
      this.wordsOnPage[0].letters = [];
      this.correct = false;
      this.wrong = false;
      this.restUse = 0;
      this.marginLineBehindLetter.left = 0;
      this.endTest = false;
      this.disabled = false;
      this.opacityStart.opacity = 1;
      this.useOnceAfterTimeZero = true
      this.marginLineBehindLetter.animationIterationCount = 10000;
      this.excessLetters = ''

      this.wpmChartArray = [];
      this.wpmChartTimeArray = [];

      this.reloaded = true


      this.loadWords();

      clearInterval(this.timer)
      clearInterval(this.checkLetterInterval)

      setTimeout(()=>{
        this.focusInput = false;
        this.$refs.focusInputWrite.focus();
      },0)
    },
  },
  // funkje ktore wywouj si po zaadowaniu stronu
  // functions that are called when the page is loaded
  async mounted() {
    this.keyboard =  this.keyboardQWERTY
    this.loadWords();
    this.$refs.focusInputWrite.focus();
    // import { _id } from '../../../backend/actions/dashboardActions'

    let { _id,login } = await import('../../../backend/actions/dashboardActions')
    let {_idd} = await require('../../../backend/actions/dashboardActions')


    this.IDuser = _id
    console.log(this.IDuser)
    console.log(_id,login,_idd)


    // alert(this.IDuser)

    // pobiera informacji o uytkowniku
    // gets user information user information
    //zrobienie osobnego geta dla kazdego wchodzacego na strone przez Math.random() i wysylanie tego id do api
    // await axios
    // .get('http://localhost:3000/dashboard.json')
    // .then((response) => {
    //   this.IDuser = response.data.user._id;
    //   console.log(response);
    //   console.log(this.IDuser)
    //   })
    // .catch(() =>        this.IDuser = '')

    // await axios
    //   .get('http://localhost:3000/dashboard.json')
    //   .then((response) => setTimeout(()=>{this.IDuser = response.data.user._id; console.log(response); console.log(this.IDuser) }  ,0))
    //   .catch(() =>        this.IDuser = '')


    // pobieranie informacji o najlepszych zajerestrowanych uytkownikach
    // gets information about the best registered users
    if(this.IDuser === '') console.log('cannot find id user', this.IDuser)
    else{
      await axios
      .get(`http://localhost:3000/dashboard/${this.IDuser}`)
      .then((response) => {
          this.infoUser = response.data
          console.log('Info about user:  ', this.infoUser)
      })
      .catch((err) => console.log(err))
    }



  },
}


</script>



<style lang="scss">
@import '../scss/variables';

*{
    box-sizing: border-box;

}


body{
    background: $bg-main;
    color: $color-strong;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin: 0;
    padding: 0;
    color: $color-sub;

}
main{
  .allWords{
    position: absolute;
    left: 60%;
    top: 30%;
    transform: translate(-50%,-50%);
    color: $color-sub;
    font-size: 1.5rem;
    font-family: 'Roboto Mono', monospace;
    width: 70%;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    padding-left: 4rem;


    .lineBehindLetter{
      height: 2.3rem;
      width: .12rem;
      padding-left: 4rem;
      border-right: solid .15rem $color-strong;
      position: absolute;
      top: -.2rem;
      animation: .8s beforeOpacity infinite;
      transition: left .08s;



      @keyframes beforeOpacity{
        0%{
            opacity: 1;
        }
        50%{
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
      }
    }

    .firstWord{

      display: flex;
      flex-direction: row;
    }

    .excessLetters{
      color: rgb(255, 117, 93);
    }


    .subWords{
      display: flex;
      flex-direction: row;
      margin-left: .8rem;
    }


  }

  .inputWrite{
    opacity: 0;
    width: 0;
    height: 0;
    position: absolute;
    outline: none;
    font-size: 2rem;
    border: none;
  }
  .inputWrite:focus{
    border: none;
  }
}

@media screen and (max-width: 1500px) and(min-width: 1025px){
    html{
        font-size: 15px;
    }
}

@media screen and (max-width: 1024px) and(min-width: 769px){
    html{
        font-size: 15px;
    }
}

@media screen and (max-width: 768px) and(min-width: 481px){
    html{
        font-size: 14px;
    }
}

@media screen and (max-width: 480px) and(min-width: 320px){
    html{
        font-size: 14px;
    }
}
@media screen and (max-width: 319px) and(min-width: 100px){
    html{
        font-size: 12px;
    }
}

</style>
