<template>
    <div class="elements">
        <div class="timeSpent">
            <span
                :class="{opacityColorTimeSpent,normalColorTimeSpent}"
                @click="opacityTimeSpent"
                :style="opacityStart"
            >{{ timeSpent }}</span>
        </div>
        <div class="flexReload">
            <button
                class="reload"
                @click="reload">
                <i class="fas fa-sync-alt"></i>
            </button>
        </div>
        <div class="shortcut">
            Shortcut : <br> Tab + enter = Restart
        </div>
    </div>
</template>
<script>

export default {
    props: ['timeSpent','opacityColorTimeSpent','normalColorTimeSpent','opacityStart'],
    name: 'Elements',
    methods: {
        opacityTimeSpent(){
            this.$emit('change-opacity-time-spent')
        },
        reload(){
            this.$emit('reload-functions')
        }
    }
}
</script>
<style lang="scss">
@import '../scss/variables';

.elements{
    width: 100%;
    margin-top: 15vh;

    .timeSpent{
        text-align: center;
    }
    .opacityColorTimeSpent{
        color: transparent;
    }
    .opacityColorTimeSpent , .normalColorTimeSpent{
        font-size: 10rem;
        text-align: center;
        opacity: .3;
        cursor: pointer;
    }

    .flexReload{
        display: flex;
        justify-content: center;
        .reload{
            font-size: 5rem;
            text-align: center;
            background: transparent;
            border: none;
            margin-top: 6vh;
            color: $color-sub;
            transition: .2s background-color;

            &:hover{
                border: none;
                outline: none;
                background-color: rgb(71, 67, 67);
            }
            &:focus{
                border: none;
                outline: none;
                background-color: rgb(71, 67, 67);
            }
        }
    }
    .shortcut{
        text-align: center;
        font-weight: 100;
        margin-top: 2vh;
    }

}

@media screen and (max-width: 768px) and(min-width: 481px){
    .elements{
        .flexReload{
            .reload{
                margin-top: 20vh;
            }
        }
    }
}

@media screen and (max-width: 480px) and(min-width: 320px){
    .elements{
        .flexReload{
            .reload{
                margin-top: 20vh;
            }
        }
    }
}
@media screen and (max-width: 319px) and(min-width: 100px){
    .elements{
        .flexReload{
            .reload{
                margin-top: 20vh;
            }
        }
    }
}


</style>