<template>
    <nav>
        <div class="navElements" v-show="!layerSett || !layerTopUsers">
        <a href="/"><span>FastTyping</span></a>
        <i class="fas fa-cog" @click="showLayerSett"></i>
        <a href="/visit"><i class="fas fa-question"></i></a>
        <i class="fas fa-tachometer-alt" @click="showLayerTopUsers"></i>
        <a href="/faq"><i class="fas fa-headset"></i></a>
        <a href="http://localhost:3000/users/login">
            <i class="fas fa-sign-in-alt"></i>
            <span class="username">{{ infoUser.name }}</span>
        </a>

        </div>
        <div class="setting" v-show="layerSett">
            <div class="alertSuccess" v-show="alertSuccess">
                Success
                <i class="fas fa-times sett cross" @click="closeAlert"></i>
            </div>

            <div class="flexCross" @click="showLayerSett">
                <i class="fas fa-times sett cross"></i>
            </div>
            <div class="settings-flex">
                <div class="grid-elements">

                    <div class="langS settings">
                        <h1 class="lang-h1">Languages</h1>
                        <div class="languages" >
                            <div
                                v-for="(lang,index) in languagesArray"
                                :key="index"
                                class="lang button"
                                @click="$emit('change-language', lang.json)"
                                >
                                {{ lang.lang }}
                            </div>

                        </div>
                    </div>

                    <div class="forLearnS settings">
                        <h1>Learning to write</h1>
                        <div class="forLearn">
                            <div
                                v-for="(char,index) in alphabetArray"
                                :key="index"
                                class="char button"
                                @click="$emit('change-language-alphabet', char.json)"
                                >
                                {{ char.char }}
                            </div>
                        </div>
                    </div>

                    <div class="timeS settings">
                        <h1 class="time-h1">Time</h1>
                            <div class="times">
                                <div
                                    class="time button"
                                    v-for="time in timeToChangeArray"
                                    :key="time"
                                    @click="$emit('change-time-spent',time)"
                                    >
                                    {{ time }}s
                                </div>
                            </div>
                        <input type="number" placeholder="Here you can enter your own time(in seconds)" max="10000" class="inputOptionalTime">
                        <div class="approveBtnOptionalTime" @click="changeTimeSpentOptional">confirm</div>
                    </div>

                    <div class="writingS settings">
                        <h1 class="writing-h1">Type of writing</h1>
                        <div class="tows">
                            <div class="button" @click="changeTypeOfWriting1" >letters and words shifting</div>
                            <div class="button" @click="changeTypeOfWriting2" >line after words moves</div>
                        </div>
                    </div>

                    <div class="live-scoreS settings">
                        <h1 class="score-h1">Live score</h1>
                        <div class="button" @click="showLiveScore">{{ showScore }}</div>
                    </div>

                    <div class="keyboardS settings">
                        <h1>Screen keyboard</h1>
                        <div class="button" @click="showKeyboardFunction">{{ showKeyboard }}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="topUsersLayer" v-show="layerTopUsers">
            <div class="flexCross">
                <i class="fas fa-times sett cross" @click="showLayerTopUsers"></i>
            </div>
            <h1><i class="fas fa-crown"></i> Global Ranking users FastTyping<i class="fas fa-crown"></i></h1>
                <div class="topUsers">
                        <div class="topUsers15">
                            <tr class="titleTr">
                                <td>Rank </td>
                                <td>Name </td>
                                <td>Time: 15</td>
                                <td>Accuracy </td>

                            </tr>
                            <tr v-for="(user, index) in topUsers15" :key="user._id" class="topUsers15Tr">
                                <td class="userPositiontd">{{ index + 1 }}</td>
                                <td >{{ user.name }} </td>
                                <td class="userWPMtd">{{ user.bestWPM15 }}wpm &nbsp;</td>
                                <td>{{ user.accuracy15 }}%</td>
                            </tr>
                            </div>
                        <div class="topUsers60">
                            <tr class="titleTr">
                                <td>Rank </td>
                                <td>Name </td>
                                <td>Time: 60 </td>
                                <td>Accuracy </td>
                            </tr>
                            <tr v-for="(user, index) in topUsers60" :key="user._id" class="topUsers60Tr">
                                <td class="userPositiontd">{{ index + 1 }}</td>
                                <td >{{ user.name }}</td>
                                <td class="userWPMtd">{{ user.bestWPM60 }}wpm &nbsp;</td>
                                <td>{{ user.accuracy60 }}%</td>
                            </tr>
                        </div>

                </div>
            </div>
    </nav>
</template>
<script>

export default {
    name: 'Menu',
    props: [
        'layerSett',
        'showScore',
        'layerTopUsers',
        'topUsers15',
        'topUsers60',
        'languagesArray',
        'infoUser',
        'timeToChangeArray',
        'alertSuccess',
        'showKeyboard',
        'showKeyboardBool',
        'alphabetArray'
        ],
    methods: {
        showLayerSett(){
            this.$emit('show-layer-sett')
        },
        // closeSettings(){
        //     this.$emit('close-settings')
        // },
        changeTimeSpentOptional(){
            this.$emit('change-time-spent-optional')
        },
        changeTypeOfWriting1(){
            this.$emit('change-type-of-writing1')
        },
        changeTypeOfWriting2(){
            this.$emit('change-type-of-writing2')
        },
        showLiveScore(){
            this.$emit('show-live-score')
        },
        showKeyboardFunction(){
            this.$emit('show-keyboard-function')
        },
        showLayerTopUsers(){
            this.$emit('show-layer-top-users')
        },
        closeAlert(){
            this.$emit('close-alert')
        }
    },
}
</script>
<style lang="scss">
@import '../scss/variables';

nav{
    text-align: center;

    a{
        text-decoration: none;
    }

    .username{
        font-size: 1rem;
        color: $color-sub;
        margin-left: 1vw;
        transition: .3s color;

        &:hover{
            color: $color-strong;
        }
    }

    span{
        color: $color-strong;
        font-weight: 600;
        font-size: 2rem;
    }

    .fas {
        margin-left: 1vw;
        transition: .3s color;
        color: $color-sub;
        font-size: 1rem;
        cursor: pointer;

        &:hover{
            color: $color-strong;
        }
    }

    .fa-cog{
        font-size: 2rem;
    }

    .setting{
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 11;
        background-color: $bg-main;
        position: absolute;

        .alertSuccess{
            position: absolute;
            background-color: rgb(162, 247, 162);
            top: 0;
            left: 0%;
            width: 20vw;
            height: 5vh;
            color: #1d8f37;
            font-size: 1.2rem;
            display: flex;
            padding: .5rem;
            flex-direction: row;
            justify-content: space-between;
            transition: .3s all;

            .fas{
                color: #1d8f37;
                font-size: 2rem;
                transition: .2s;

                &:hover{
                    color: #000;
                }
            }
        }

        .flexCross{
            display: flex;
            justify-content: flex-end;
            cursor: pointer;
            top: 0;

            .cross{
                text-align: center;
                line-height: 5vh;
                font-size: 2rem;
                width: 5vh;
                height: 5vh;
                transition: all .5s;
                background-color: $color-strong;
                color: $bg-main;


                &:hover{
                    opacity: 0.7;
                }
            }
        }

        .settings-flex{
            height: 90%;
            display:flex;
            align-items:center;
            justify-content:center;

            .grid-elements{
                display: grid;
                grid-template-columns: 40vw 40vw;
                grid-row: auto auto;
            }

            .langS{
                text-align: left;
                .lang{
                    display: inline-block;
                    width: 30%;
                }
            }
            .timeS{
                text-align: left;
                .time{
                    display: inline-block;
                    width: 10%;
                }
            }
            .live-scoreS{
                text-align: left;
                .live-score{

                }
            }
            .writingS{
                .tows{

                }
            }
            .forLearnS{
                text-align: center;
                .forLearn{
                    width: 38vw;
                    .char{
                        display: inline-block;
                        margin-left: 3vw;
                        font-family: 'Roboto Mono', monospace;
                    }

                }
            }
            .keyboardS{
                text-align: center;

            }

            .settings{
                padding-bottom: 8vh;

                input[type="number"]::-webkit-outer-spin-button,
                input[type="number"]::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                }
                .inputOptionalTime{
                    background-color: transparent;
                    outline: none;
                    border: none;
                    border-bottom: 2px solid #5e5c5cb9;
                    color: $zaje-color;
                    margin-top: 3vh;
                    font-size: 1.2rem;
                    width: 60%;
                }
                .approveBtnOptionalTime{
                    display: inline-block;
                    border: 2px solid #5e5c5cb9;
                    cursor: pointer;
                    width: 10%;
                    height: 15%;
                    margin-left: 1%;
                    padding: 4px;
                    transition: .3s background-color;

                    &:hover{
                        background-color: #5e5c5cb9;
                    }
                }


                &:nth-child(1){
                    border-right: 1px solid #5e5c5cb9;
                    border-bottom: 1px solid #5e5c5cb9;
                }
                &:nth-child(2){
                    border-left: 1px solid #5e5c5cb9;
                    border-bottom: 1px solid #5e5c5cb9;
                }
                &:nth-child(3){
                    border-right: 1px solid #5e5c5cb9;
                    border-top: 1px solid #5e5c5cb9;
                    border-bottom: 1px solid #5e5c5cb9;
                }
                &:nth-child(4){
                    border-left: 1px solid #5e5c5cb9;
                    border-top: 1px solid #5e5c5cb9;
                    border-bottom: 1px solid #5e5c5cb9;
                }
                &:nth-child(5){
                    border-top: 1px solid #5e5c5cb9;
                    border-right: 1px solid #5e5c5cb9;
                }
                &:nth-child(6){
                    border-left: 1px solid #5e5c5cb9;
                    border-top: 1px solid #5e5c5cb9;
                }

                h1{
                    color: $color-strong;
                }

                .button{
                    font-size: 1.4rem;
                    padding: 5px;
                    transition: all .2s;
                    cursor: pointer;
                    color: $color-strong-sub;


                    &:hover{
                        color: $color-strong;
                    }
                }

            }
        }
    }

    .topUsersLayer{
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 11;
        background-color: $bg-main;
        position: absolute;

        .flexCross{
            display: flex;
            justify-content: flex-end;
            cursor: pointer;
            top: 0;

            .cross{
                text-align: center;
                line-height: 5vh;
                font-size: 2rem;
                width: 5vh;
                height: 5vh;
                transition: all .5s;
                background-color: $color-strong;
                color: $bg-main;


                &:hover{
                    opacity: 0.7;
                }
            }
        }
        h1{
            font-size: 4rem;
            color: $color-strong;

            .fas{
                font-size: 4rem;
                color: $color-strong;
            }
        }

        .topUsers{
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            flex-wrap: wrap;
            .topUsers15, .topUsers60{
                .titleTr{
                    color: $zaje-color;
                    font-size: 1.1rem;
                    font-weight: 700;
                }

                .topUsers15Tr, .topUsers60Tr{
                    td{
                        color: $zaje-color;
                        font-weight: 100;
                        font-size: 2rem;
                    }
                    .userWPMtd{
                        font-weight: bold;
                    }
                    .userPositiontd{
                        color: $color-strong;
                    }
                }
            }

            td{
                border-bottom: 1px solid rgb(180, 179, 179);
            }


        }
    }
}


@media screen and (max-width: 1500px) and(min-width: 1025px){
    html{
        font-size: 14px !important;

        body{
            .topUsersLayer{
                .topUsers15{
                    margin-top: 10vh;
                }
                .topUsers60{
                    margin-top: 10vh;
                }
            }
        }
    }

}

@media screen and (max-width: 1024px) and(min-width: 769px){
    html{
        font-size: 13px !important;

        body{
            .topUsersLayer{
                .topUsers15{
                    margin-top: 10vh;
                }
                .topUsers60{
                    margin-top: 10vh;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) and(min-width: 481px){
    html{
        font-size: 8px !important;

        body{
            .topUsersLayer{
                .topUsers60{
                    margin-top: 10vh;
                }
                .topUsers60{
                    margin-top: 10vh;
                }
            }
        }
    }
}

@media screen and (max-width: 480px) and(min-width: 320px){
    html{
        font-size: 6px !important;

        body{
            .topUsersLayer{
                .topUsers60{
                    margin-top: 10vh;
                }
                .topUsers60{
                    margin-top: 10vh;
                }
            }
        }
    }
}
@media screen and (max-width: 319px) and(min-width: 100px){
    html{
        font-size: 5px !important;

        body{
            .topUsersLayer{
                .topUsers60{
                    margin-top: 10vh;
                }
                .topUsers60{
                    margin-top: 10vh;
                }
            }
        }
    }
}


</style>